import axios from 'axios';
import { isLoggedIn } from '../auth';

/**
* Gets a list of courses
* @returns {Promise} response - promise of courses to be resolved
*/
export function getCourses(limit, offset) {
	if (isLoggedIn()) {
		return axios.get(`/v1/classes?limit=${limit}&offset=${offset}`);
	}
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403, statusText: 'Unauthorised'
	}));
}

export function getHomePageCourses(limit, offset) {
	const status = 1;
	return axios.get(`/v1/classes/filter?status=${status}&limit=${limit}&offset=${offset}`);
}

/**
* Gets a list of filtered courses
* @param {String} - status query type of classes to be filtered
* @returns {Promise} response - promise of filtered courses to be resolved
*/
export function getFilteredCourses(status, careerId) {
	if (isLoggedIn()) {
		return axios.get(`/v1/classes/filter?status=${status}&career_id=${careerId}`);
	}
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403, statusText: 'Unauthorised'
	}));
}

/**
 * Fetches the a course using the id
 * @returns {Promise} class - a promise of course to be resolved.
 */
export function getCourse(id, userId) {
	if (isLoggedIn()) {
		return axios.get(`/v1/classes/${id}?userId=${userId}`);
	}
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403, statusText: 'Unauthorised'
	}));
}

/**
 * Fetches a course forum using the id of the course
 * @returns {Promise} class - a promise of course to be resolved.
 */
export function getCourseDiscussion(id) {
	if (isLoggedIn()) {
		return axios.get(`/v1/classes/${id}/forum`);
	}
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403, statusText: 'Unauthorised'
	}));
}

/**
 * Create a course forum
 * @param {Number} id - id of course forum
 * @param {Object} data - object with the content of forum
 * @returns {Promise} a promise of the course forum to be created
 */
export function createCourseForum(id, data) {
	if (isLoggedIn()) {
		return axios.post(`/v1/classes/${id}/forum`, data);
	}
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403, statusText: 'Unauthorised'
	}));
}

