/**
 * Allows a student to join a course
 * @param {Number} id - id of the student
 */
export function joinCourse(id, data) {
	if (isLoggedIn()) {
		return axios.post(`/v1/classes/${id}/learners`, data);
	}
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403, statusText: 'Unauthorised'
	}));
}

export function getDiscussionThread(id, discussionId) {
	if (isLoggedIn()) {
		return axios.get(`/v1/classes/${id}/forum/${discussionId}`)
			.then(response => response);
	}
	return Promise.resolve(new Response(
		{ message: 'Unauthorised' },
		{ status: 403, statusText: 'Unauthorised' }
	));
}

